.btn {
  border: none;
  background: #2A2A2A;
  color: #AAAAAA;
  padding: 7px 20px;
  font-size: 20px;
  border-radius: 5px;
  font-weight: 700;
}

.btn:hover {
  box-shadow: 0 0 3px 1px #fff;
}

.btn:focus {
  outline: none;
}

.AppProfilePosts {
  padding: 20px;
}

.AppProfilePostsTitle {
  font-size: 40px;
  margin-bottom: 20px;
}

.AppProfilePostsInput textarea {
  width: 100%;
  height: 100px;
  font-size: 18px;
  padding: 10px 15px;
  box-sizing: border-box;
  resize: none;
  border-radius: 5px;
  background: #6f767b;
  color: #ffffff;
}

.AppProfilePostsInput textarea::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #cecece;
  opacity: 1; /* Firefox */
}

.AppProfilePostsInput textarea:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #cecece;
}

.AppProfilePostsInput textarea::-ms-input-placeholder { /* Microsoft Edge */
  color: #cecece;
}

.AppProfilePostsInput textarea:focus {
  outline: none;
}

.AppProfilePostsBtn {
  margin-left: auto;
  display: block;
  margin-top: 10px;
  cursor: pointer;
}

.AppProfilePostsList {
  margin-top: 20px;
}

.AppProfilePostsItem {
  display: flex;
  margin-bottom: 20px;
  background: #6F767B;
  padding: 10px;
  border-radius: 100px;
}

.AppProfilePostsItemIcon {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50%;
}

.AppProfilePostsItemIcon img {
  width: 100%;
}

.AppProfilePostsItemMessage {
  flex-grow: 1;
  margin-left: 10px;
  font-size: 20px;
  display: flex;
  align-items: center;
}